// Buttons
//––––––––––––––––––––––––––––––––––––––––––––––––––

.button,
button,
input[type="submit"],
input[type="reset"],
input[type="button"] {
  display: inline-block;
  height: 38px;
  padding: 0 30px;
  color: var(--secondary-color);
  text-align: center;
  font-size: 11px;
  font-weight: 600;
  line-height: 38px;
  letter-spacing: .1rem;
  text-transform: uppercase;
  text-decoration: none;
  white-space: nowrap;
  background-color: transparent;
  border-radius: var(--global-radius);
  border: 1px solid var(--border-color);
  cursor: pointer;
  box-sizing: border-box;
}

input {
  &[type="submit"],
  &[type="reset"],
  &[type="button"] {
    display: inline-block;
    height: 38px;
    padding: 0 30px;
    color: var(--secondary-color);
    text-align: center;
    font-size: 11px;
    font-weight: 600;
    line-height: 38px;
    letter-spacing: .1rem;
    text-transform: uppercase;
    text-decoration: none;
    white-space: nowrap;
    background-color: transparent;
    border-radius: var(--global-radius);
    border: 1px solid var(--border-color);
    cursor: pointer;
    box-sizing: border-box;
  }
}

.button:hover,
button:hover {
  color: var(--dark-grey);
  border-color: color(var(--dark-grey) lightness(33.5%));
  outline: 0;
}

input {
  &[type="submit"]:hover,
  &[type="reset"]:hover,
  &[type="button"]:hover {
    color: var(--dark-grey);
    border-color: color(var(--dark-grey) lightness(33.5%));
    outline: 0;
  }
}

.button:focus,
button:focus {
  color: var(--dark-grey);
  border-color: color(var(--dark-grey) lightness(33.5%));
  outline: 0;
}

input {
  &[type="submit"]:focus,
  &[type="reset"]:focus,
  &[type="button"]:focus {
    color: var(--dark-grey);
    border-color: color(var(--dark-grey) lightness(33.5%));
    outline: 0;
  }
}

.button.button-primary,
button.button-primary {
  color: #fff;
  background-color: var(--primary-color);
  border-color: var(--primary-color);
}

input {
  &[type="submit"].button-primary,
  &[type="reset"].button-primary,
  &[type="button"].button-primary {
    color: #fff;
    background-color: var(--primary-color);
    border-color: var(--primary-color);
  }
}

.button.button-primary:hover,
button.button-primary:hover {
  color: #fff;
  background-color: var(--link-color);
  border-color: var(--link-color);
}

input {
  &[type="submit"].button-primary:hover,
  &[type="reset"].button-primary:hover,
  &[type="button"].button-primary:hover {
    color: #fff;
    background-color: var(--link-color);
    border-color: var(--link-color);
  }
}

.button.button-primary:focus,
button.button-primary:focus {
  color: #fff;
  background-color: var(--link-color);
  border-color: var(--link-color);
}

input {
  &[type="submit"].button-primary:focus,
  &[type="reset"].button-primary:focus,
  &[type="button"].button-primary:focus {
    color: #fff;
    background-color: var(--link-color);
    border-color: var(--link-color);
  }
  &[type="email"],
  &[type="number"],
  &[type="search"],
  &[type="text"],
  &[type="tel"],
  &[type="url"],
  &[type="password"] {
    height: 38px;
    padding: 6px 10px; // The 6px vertically centers text on FF, ignored by Webkit
    background-color: #fff;
    border: 1px solid lighten(border-color, 8.8%);
    border-radius: var(--global-radius);
    box-shadow: none;
    box-sizing: border-box;
  }
}



